*{
  scroll-behavior: smooth;
}
.app{
  min-height: 100vh;
  background-color: #39445a;
  color: white;
  padding-top: 130px;
  padding-bottom: 70px;
}
@media (max-width:1000px) {
  .app{
    padding-top: 70px;
  }
}
.pageTitle{
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-family: "Montserrate",sans-serif;
  padding: 4px;
  border-radius: 50px;
  color: white;
}
@media (max-width:1000px){
  .pageTitle{
    font-size: 6.4vw;
  }
}