@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  scroll-behavior: smooth;
}
.app{
  min-height: 100vh;
  background-color: #39445a;
  color: white;
  padding-top: 130px;
  padding-bottom: 70px;
}
@media (max-width:1000px) {
  .app{
    padding-top: 70px;
  }
}
.pageTitle{
  text-transform: uppercase;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: "Montserrate",sans-serif;
  padding: 4px;
  border-radius: 50px;
  color: white;
}
@media (max-width:1000px){
  .pageTitle{
    font-size: 6.4vw;
  }
}
.header{
    width: 100%;
    cursor: pointer;
    position: fixed;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    text-transform: uppercase;
    background-color: #39445a;
    font-family: 'montserrat',sans-serif;
    font-size: 5vw;
    padding-bottom: 15px;
    box-shadow: 0px 1px 5px black;
    color: white;
    z-index: 100;
}
@media (max-width:1000px) {
    .header{
        padding-top: 15px;
        font-size: 6.4vw;
    }
}
.media{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 200px;
    padding: 5px;
    margin: 5px 0;
    background-color: #282c34;
    border-radius: 10px;
    position: relative;
    font-family: "Montserrate", sans-serif;
}
.media:hover{
    background-color: white;
    color: black;
}
@media (max-width:550ps) {
    .media{
        width: 46%;
    }
}
.poster{
    border-radius: 10px;
}
.title{
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
}
.subTitle{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-bottom: 3px;
    padding: 0 2px;   
}


.ContentModel_landscape{
    object-fit: contain;
    border-radius: 10px;
}
.Content_portrait{
    display: none;
    object-fit: contain;
    border-radius: 10px;
}
.tagline{
    padding-bottom: 10px;
    -webkit-align-self: center;
            align-self: center;
}
.ContentModel{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 100%;
    width: 100%;
    overflow-y: scroll;
    color: white;
}
.ContentModel::-webkit-scrollbar{
    display: none;
}
.ContentModel_about{
    padding: 10px;
    width: 95%;
    height: 90%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    font-family: "Roboto",sans-serif;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    font-weight: 300;
}
.ContentModel_title{
    height: 12%;
    font-size: 4vw;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.ContentModel_description{
    display: -webkit-flex;
    display: flex;
    height: 40%;
    overflow-y: scroll;
    padding: 15px;
    border-radius: 20px;
    scrollbar-width: thin;
    box-shadow: inset 0 0 5px #000000;
    text-align: justify;
}
.ContentModel_description::-webkit-scrollbar{
    display: none;
}
@media (min-width:835px){
    .ContentModel_landscape{
        display: none;
    }
    .Content_portrait{
        display: -webkit-flex;
        display: flex;
        width: 38%;
    }
    .ContentModel{
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        padding: 10px 0;
    }
    .ContentModel_about{
        width: 58%;
        padding: 0;
        height: 100%;
    }
    .ContentModel_title{
        font-size: 3.5vw;
    }
    .ContentModel_description{
        font-size: 22px;
    }
}
.carouselItem{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    object-fit: contain;
    padding: 10px;
}
.carouselItem_img{
    border-radius: 10px;
    margin-bottom: 5px;
    box-shadow: 0 0 5px black;
}
.trending{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
